<template>
  <div id="courseware">
    <head-top title="课件"></head-top>
    <div class="main">
      <div v-for="(item, index) in dataList" :key="index" class="item" @click="download(item)">
        <div class="fL">
          <i class="iconfont iconword"></i>
          <span
            class="title time"
          >{{item.Value['MaterialName'] + '.' + item.Value['MaterialFileExt']}}</span>
        </div>
        <div class="size time fR">
          <span
            v-if="item.Value['MaterialFileSize']>1024*1024"
          >{{ Math.round( item.Value['MaterialFileSize']/ 1024.0/1024 )}} MB</span>
          <span v-else>{{ Math.round( item.Value['MaterialFileSize']/ 1024) }} KB</span>
        </div>
      </div>

      <van-loading v-if="loading" type="spinner" color="#1989fa">加载中...</van-loading>
      <r-lack-data v-else-if=" !dataList || dataList.length === 0"></r-lack-data>
    </div>
  </div>
</template>

<script>
import { Loading } from "vant";

import headTop from "@/components/headTop";
export default {
  name: "",
  components: { headTop, [Loading.name]: Loading },
  props: {},
  data() {
    return {
      dataList: [],
      loading: false
    };
  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      this.getDataList();
    });
  },
  methods: {
    getDataList() {
      this.dataList = [];
      this.loading = true;
      this.$mainHttp.courseware
        .getList({
          courseID: this.$store.getters.courseID
        })
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.dataList.push(item);
            });
          }
          this.loading = false;
        })
        .catch(err => {
          this.loading = false;
        });
    },
    download(item) {
      window.location.href = this.$mainHttp.resourse.file(
        item.Value["MaterialID"]
      );

      this.$toast("下载开始~");
    }
  },
  computed: {},
  watch: {}
};
</script>

<style lang='less' scoped>
#courseware {
  width: 100%;
  height: 100%;
  .main {
    width: 100%;
    background: #ffffff;
    overflow: hidden;
    .item {
      overflow: hidden;
      width: 92vw;
      height: 6vh;
      line-height: 6vh;
      margin: 0 auto;
      border-bottom: 1px solid #dddddd;
      span,
      i {
        display: inline-block;
      }
      .iconfont {
        font-size: 1.7rem;
        color: blue;
      }
    }
  }
}
</style>


